import React, { useMemo } from 'react'
import { useFormik } from 'formik'
import { isArray, range, shuffle } from 'lodash';

// import imgSize from '../TrashPage/data/imgSize';
import useData from '../TrashPage/data/useData'

import Box from '../../components/Box';
import Link from '../../components/Link';
import BackgroundImage from '../../components/BackgroundImage';
import Flex from '../../components/Flex';
import theme, { responsive } from '../../components/ThemeProvider/theme';
import FilterAndSearch from './FilterAndSearch';

import useResponsive from '../../contexts/mediaQuery/useResponsive'

import images from './images'
import { SizeMe } from 'react-sizeme';

const Catalogue = ({ shouldFix }) => {
  const { values, handleChange, setFieldValue } = useFormik({
    initialValues: {
      search: '',
    },
  })
  const data = useData()
  const filteredData = useMemo(() => {
    return data ? data.filter(d => {
      // if (!images[d.name]) console.log(d.name)
      return images[d.name]
    }).map(d => {
      const isDisabled = Object.keys(values).reduce((res, key) => {
        if (!values[key]) return res
        if (key === 'search') return res || !new RegExp(values[key]).test(d.name)
        return res || (!d[key] || !(isArray(d[key]) ? d[key].includes(String(values[key])) : d[key] === values[key]))
      }, false)
      return {
        ...d,
        img: images[d.name][d.name],
        isDisabled,
      }
    }) : []
  }, [data, values])
  const { getCurrentValue } = useResponsive()
  const perRow = getCurrentValue(responsive(4, 8))
  // console.log(perRow)
  const layout = useMemo(() => {
    // const totalSize = filteredData.reduce((total, item) => total + item.size[0] * item.size[1], 0)
    // console.log(totalSize, totalSize / 8)
    const grid = []
    const sorted = []
    const queue = shuffle(filteredData.filter(d => !d.isDisabled))

    const markPos = (x, y) => {
      grid[y] = grid[y] || range(perRow).fill(null)
      grid[y][x] = 1
    }

    const markSpace = ([x, y], size) => {
      for (let i = 0; i < size[0]; i++) {
        for (let j = 0; j < size[1]; j++) {
          markPos(x + i, y + j)
        }
      }
    }

    const getRestX = (pos) => {
      grid[pos[1]] = grid[pos[1]] || range(perRow).fill(null)
      if (grid[pos[1]][pos[0]]) return 0
      const toFind = grid[pos[1]].slice(pos[0])
      const emptySpace = toFind.findIndex(Boolean)
      return emptySpace > -1 ? emptySpace : toFind.length
    }

    const savePosition = (data, pos) => {
      data.pos = pos
      sorted.push(data)
      markSpace(pos, data.size)
    }

    let x = 0
    let y = 0
    let tries = 0
    while (queue.length > 0) {
      let restX = getRestX([x, y])
      while (restX < 1) {
        if (x === perRow) {
          x = 0
          y += 1
        } else {
          x += 1
        }
        restX = getRestX([x, y])
      }
      const data = queue.splice(0, 1)[0]
      if (data.size[0] <= restX) {
        tries = 0
        savePosition(data, [x, y])
        x += data.size[0]
      } else {
        if (tries > 20) {
          x = 0
          y += 1
        } else {
          tries += 1
          queue.push(data)
        }
      }
    }
    // console.log(sorted)
    return sorted
  }, [filteredData, perRow])
  // console.log(filteredData)
  return (
    <Box height="100%" bg="white">
      <Box position={shouldFix ? 'fixed' : 'absolute'} top={theme.headerHeight} left="0" right="0" bg="white" px="2em" zIndex="dropdown">
        <FilterAndSearch onChange={handleChange} setFieldValue={setFieldValue} values={values} />
      </Box>
      <Box pt={theme.headerHeight}>
        <SizeMe>
          {({ size }) => (
            <Box.Relative mt="1em">
              {layout.map(d => {
                const gridSize = size.width / perRow
                return d.isDisabled ? null : (
                  <Box.Absolute key={d.id} style={{ width: gridSize * d.size[0], left: d.pos[0] * gridSize, top: d.pos[1] * gridSize }} p="0.25em">
                    <Link to={`/trash/${d.id}`} target="_blank">
                      <BackgroundImage ratio={d.size[0] / d.size[1]} src={d.img} />
                    </Link>
                  </Box.Absolute>
                )
              })}
            </Box.Relative>
          )}
        </SizeMe>
      </Box>
    </Box>
  )
}

export default Catalogue
